import React from "react";
import { NavLink, Outlet } from "react-router-dom";
import "./ComMenu.css";
export default function ComMenu(props) {
  return (
    <>
      <div className="ComMenu">
        {props.menus.map((item) => {
          console.log(item.title, item.to, "item.title");
          return (
            <NavLink to={item.to} key={item.title}>
              {item.title}
            </NavLink>
          );
        })}
      </div>
      <div className="com-content">
        <Outlet />
      </div>
    </>
  );
}
