import React from "react";
import { connect } from "react-redux";
import { useTranslation } from "react-i18next";
import ComMenu from '@/components/comMenu/ComMenu'
import Swal from "sweetalert2";
import * as countActions from "@/redux/actions/count";

function UserCenter (props) {
  const { t } = useTranslation();
  const add = () => {
    props.increment(1);
  };
  const addAsync = () => {
    props.incrementAsync(1, 1000);
  };
  const menus = [
    {
      title: '概要',
      to: 'profile'
    },
    {
      title: 'SSH秘钥',
      to: 'ssh'
    },
    {
      title: 'API',
      to: 'api'
    },
    {
      title: '操作日志',
      to: 'log'
    },
  ]
  const showDialog = () => {
    Swal.fire({
      title: "success!",
      text: "Do you want to continue",
      icon: "success",
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      showCancelButton: true,
      // reverseButtons: true // 翻转确定，取消
    }).then((isConfirm) => {
      try {
        //判断 是否 点击的 确定按钮
        if (isConfirm.value) {
          Swal.fire("成功", "点击了确定", "success");
        } else {
          Swal.fire("取消", "点击了取消", "error");
        }
      } catch (e) {
        alert(e);
      }
    });
  };
  return (
    <>
      <ComMenu menus={menus}></ComMenu >
      <div>
        {t("header.home")}
        <p>测试 redux </p>
        <button onClick={add}>点击加1</button>
        <button onClick={addAsync}>1秒后加</button>
        <p>{props.count}</p>
        <button onClick={showDialog}>交互效果</button>
      </div>
    </>
  );
}

// 导出容器组件
export default connect(
  (state) => ({
    count: state.count,
  }),
  countActions
)(UserCenter);
